html,
body,
* {
  box-sizing: border-box;
  font-size: 0.16rem;
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-family: "Microsoft YaHei";
  line-height: 1;
}

html,
body {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
  color: #333;
}

input,
button {
  min-height: 0.013333rem;
}

li {
  list-style-type: none;
}

i {
  font-style: normal;
}

