
.mint-indexlist-content {
  margin-right: 0!important;
}
.router-link-exact-active p {
  color: #f09435 !important;
}
.mu-tab-link-highlight {
  background-image: -webkit-gradient(linear, left top, right top, from(#f09435), to(#eb602b));
  background-image: -webkit-linear-gradient(left, #f09435 0%, #eb602b 100%);
  background-image: -moz-linear-gradient(left, #f09435 0%, #eb602b 100%);
  background-image: -o-linear-gradient(left, #f09435 0%, #eb602b 100%);
  background-image: linear-gradient(to right, #f09435 0%, #eb602b 100%);
}
.mint-indexlist-nav {
  background-color: transparent!important;
  border-left: none;
}
.mint-indexlist-navitem {
  padding: 0.126667rem 0.08rem;
}
.mint-indexlist-navitem {
  font-size: 0.426667rem;
}
.mint-msgbox {
  width: 70%;
}
.mint-msgbox-message {
  font-size: 0.426667rem;
}
.mint-msgbox-message {
  line-height: 1rem;
}
.mint-msgbox-btns {
  height: 0.666667rem;
}
.mint-msgbox-cancel {
  font-size: 0.426667rem;
}
.mint-msgbox-confirm {
  font-size: 0.426667rem;
}
.mint-msgbox-title {
  font-size: 0.426667rem;
}


